<template>
    <v-container fluid>
        <div v-if="Object.keys(types).length===0 && !loading">暂无数据</div>
        <div v-else>
            <v-card class="pa-2">
                <div class="d-flex justify-space-between">
                    <v-card-title class="pt-0 pb-0 ps-2">分类</v-card-title>
                    <v-card-title class="pt-0 pb-0 pe-2">
                        共<span class="display-1 d-inline primary--text">{{Object.keys(types).length}}</span>个
                    </v-card-title>
                </div>
                <v-chip-group column active-class="primary--text" v-model="selectedType">
                    <div v-if="loadingTypes">
                        <v-skeleton-loader type="chip"/>
                    </div>
                    <div v-else>
                        <v-chip filter filter-icon="mdi-view-list" v-for="type in Object.keys(types)" :key="type">
                            {{type+" ("+types[type]+")"}}
                        </v-chip>
                    </div>
                </v-chip-group>
            </v-card>
            <ArticlePreview @transferCurrentPage="getCurrentPage" :blog-articles="blogArticles" :total-page="totalPages"
                            :loading="loading"/>
        </div>
    </v-container>
</template>

<script>
    import ArticlePreview from "@/components/ArticlePreview";

    export default {
        name: "Type",
        components: {ArticlePreview},
        props: {
            paramType: {
                type: String
            }
        },
        data: () => ({
            types: Object,
            selectedType: Number,
            selectedTypesList: [],
            blogArticles: [],
            totalPages: 0,
            loading: true,
            loadingTypes: true
        }),
        methods: {
            initParamType() {
                console.log("initParamType paramType", this.paramType)
                console.log("initParamType this.types", this.types)
                console.log("initParamType this.paramType", this.paramType)
                let keys = Object.keys(this.types);
                for (let i = 0; i < keys.length; i++) {
                    if (keys[i] === this.paramType) {
                        console.log(i, keys[i], this.paramType)
                        this.selectedType = i
                        break
                    }
                }
            },
            getAllTypes() {
                let _this = this
                this.axios.get(this.API.URL.QUERY_TYPE_WITH_COUNT, {headers: {'Content-Type': 'multipart/form-data'}})
                    .then(function (result) {
                        _this.loadingTypes = false
                        let data = result.data
                        if (data.success) {
                            _this.types = data.data
                            if (typeof _this.paramType !== "function") {
                                _this.initParamType()
                            } else {
                                _this.selectedType = 0
                            }
                        }
                        console.log(result)
                    }, function (error) {
                        _this.loadingTypes = false
                        console.log(error)
                    })
            },
            getArticles(currentPage) {
                this.loading = true
                let _this = this
                this.axios.get(this.API.URL.QUERY_ARTICLE_BY_TYPE + "&types=" + this.selectedTypesList + "&page=" + (currentPage - 1), {headers: {'Content-Type': 'multipart/form-data'}})
                    .then(function (result) {
                        _this.loading = false
                        let data = result.data
                        if (data.success) {
                            console.log(data)
                            _this.totalPages = data.data.totalPages
                            _this.blogArticles = data.data.data
                            console.log("_this.blogArticles", _this.blogArticles)
                        }
                    }, function (error) {
                        _this.loading = false
                        console.log(error)
                    })
            },
            getCurrentPage(currentPage) {
                this.getArticles(currentPage)
                console.log("从ArticlePreview传过来的当前page为", currentPage)
            }
        },
        mounted() {
            this.getAllTypes()
        },
        watch: {
            selectedType() {
                let keys = Object.keys(this.types)
                this.selectedTypesList = []
                this.selectedTypesList.push(keys[this.selectedType])
                this.getArticles(1)
            },
            paramType: {
                handler() {
                    let _this = this
                    //避免首次获取不到
                    setTimeout(() => {
                        if (Object.keys(_this.types).length !== 0) {
                            _this.initParamType()
                        }
                    }, 300)
                },
                // 首次进入页面即执行
                immediate: true
            }
        },
    }
</script>

